a {
  text-decoration: none; }

.main-content {
  position: relative;
  max-width: 100%;
  overflow: hidden; }

.main-btn {
  display: inline-block;
  min-width: 320px;
  background-color: #ea2d2e;
  text-align: center;
  color: #fff;
  padding: 10px 0 10px 0;
  border-radius: 5px;
  font-size: 18px;
  font-weight: normal;
  letter-spacing: 1px; }
  .main-btn:hover {
    text-decoration: underline; }

.main-text {
  font-size: 17px;
  letter-spacing: 1px;
  word-spacing: -2px;
  line-height: 125%; }

.header {
  height: 60px;
  padding: 0 3.3% 0 3.3%;
  border-bottom: 1px solid #cbcbcb;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 5;
  background-color: #fff; }

.nav {
